.board {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 750px;
  height: 100%;
}

.board__reservationBoardItem {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  width: 100%;
  -ms-flex-align: center;
      align-items: center;
}

.board__reservationBoardItem__room {
  width: 10rem;
  margin: 0 1rem;
}
.board__reservationBoardItem__reservedBoard {
  width: 100%;
}

@media screen and (max-width: 1024px) {
  .board {
    width: 80%;
  }
}
@media screen and (max-width: 600px) {
  .board {
    width: 100%;
  }
  .board__reservationBoardItem {
    -ms-flex-direction: column;
        flex-direction: column;
  }
  .board__reservationBoardItem__room {
    margin: 0 auto 2rem;
  }
}



